import axios from "axios";

export default {
    getTriage() {
        return new Promise((resolve, reject) => {
            axios.get('https://192.168.1.6:8443/')
                .then((respuesta) => {
                    if (respuesta.status === 200) {
                        resolve(respuesta.data);
                    } else {
                        resolve([]);
                    }                    
                })
                .catch((error) => {
                    console.log('Ha ocurrido un error al recuperar el triage: ', error);
                    resolve([])
                });
        });
    }
}


